/*
		Combination of http://meyerweb.com/eric/tools/css/reset/, http://github.com/necolas/normalize.css, http://ru.html5boilerplate.com and personal experience
*/

html, body, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote,
pre, abbr, acronym, address, big, cite, code, del, dfn, img, ins, kbd, q,
samp, sub, sup, tt, var, dl, dt, dd, ol, ul, li, fieldset, form, label,
legend, table, caption, tbody, tfoot, thead, tr, th, td,  canvas, embed,
figcaption, hgroup,  output, ruby, summary, time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	vertical-align: baseline;
}

//Special reset for WA site

applet:not([class]), object:not([class]), iframe:not([class]), h1:not([class]), h2:not([class]), h3:not([class]), h4:not([class]), h5:not([class]), h6:not([class]), p:not([class]), blockquote:not([class]),
pre:not([class]), abbr:not([class]), acronym:not([class]), address:not([class]), big:not([class]), cite:not([class]), code:not([class]), del:not([class]), dfn:not([class]), img:not([class]), ins:not([class]), kbd:not([class]), q:not([class]),
samp:not([class]), sub:not([class]), sup:not([class]), tt:not([class]), var:not([class]), dl:not([class]), dt:not([class]), dd:not([class]), ol:not([class]), ul:not([class]),li:not([class]), fieldset:not([class]), 
legend:not([class]), table:not([class]), caption:not([class]),  canvas:not([class]), embed:not([class]),
figcaption:not([class]), hgroup:not([class]),  output:not([class]), ruby:not([class]), summary:not([class]), time:not([class]), mark:not([class]), audio:not([class]){
	margin: initial;
	padding: initial;
}

html, body, fieldset, form, label,tbody, tfoot, thead, tr, th, td, {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	vertical-align: baseline;
}


/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}

/*
 * Prevents iOS text size adjust after orientation change, without disabling user zoom
 *    www.456bereastreet.com/archive/201012/controlling_text_size_in_safari_for_ios_without_disabling_user_zoom/
 */
html {
		-webkit-text-size-adjust: 100%;
		-ms-text-size-adjust: 100%;
}

html, body {
	width: 100%;
	// height: 100%;
}

// body {
