// }

// ol, ul {
// 	list-style: none;
// }
ol,
ul{
	list-style: none;
	margin: 0;
	padding: 0;
}

ol:not([class]),
ul:not([class]) {
	list-style: initial;
	margin: initial;
	padding: initial;
}

