/* Styles Start*/

.feedbacks {

	&__wrapper {
		font-size: 0;
	}

	&__col {
		display: inline-block;
		vertical-align: top;
	}


}

.feedback {
	color: $dark;
	@include border-silver;
	// padding: 30px 24px 30px 30px;
	padding: 30px 24px 25px 30px;
	
	&__head,
	&__user-type {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: 20px;
	}

	&__head {
		
	}

	&__date,
	&__type {
		font-size: 1.2rem;
	}

	&__user-type {

	}

	&__user-name,
	&__content {
		font-size: 1.4rem;

	}

	&__type {
		color: $grey;
		display: block;
		padding: 1px 10px 1px 10px;
		border: 1px solid $silver;
		border-radius: 5px;

		&:hover {
			color: $dark;
		}
	}

	&__user-icon,
	&__user-name {
		display: inline-block;
		vertical-align: middle;
	}

	&__user-icon {
		position: relative;
		width: 40px;
		height: 40px;

		img {
			max-width: 100%;
			max-height: 100%;
			@include img-center;
		}
	}

	&__user-name {
		color: $dark;
		font-weight: 700;
		margin-left: 20px;
	}

	&__content_video {
		position: relative;
		border-radius: 10px;
		overflow: hidden;

		.youtube-player {
			position: relative;
			z-index: 2;
		}
	}

	&__content_audio {
		// background-color: pink;
	}

	&__video-descr {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.51), rgba(0, 0, 0, 0.0));
		z-index: 3;
		color: #fff;
		font-size: 1.7rem;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
		padding: 11px 20px 8px 20px;
	}

	&__content_text {
		.visible-content {
			display: inline-block;
			position: relative;
		}

		.moreelipses {
			display: block;
			position: absolute;
			// left: 0;
			// right: 0;
			// bottom: 0;
			// height: 50px;
			// background: linear-gradient(to bottom, rgba(255,255,255,0) 0%,rgba(255,255,255,0.74) 74%,rgba(255,255,255,1) 100%);
			// transition: all .3s ease .3s;
			// &.hide {
			// 	opacity: 0;
			// 	// transition-delay: .3s;
			// }
		}
	}

	.remaining-content {
		.remained-text {
			display: block;
			// display: none;
			max-height: 0;
			transition: all .3s;
			overflow: hidden;

			&.show {
				max-height: 1000px;
			}
		}

		.morelink {
			display: flex;
			align-items: center;
			justify-content: center;
			margin-left: auto;
			margin-right: auto;
			margin-top: 8px;
			font-size: 1.3rem;
			color: $grey;
			position: relative;
			

			.morelink__text {
				position: relative;
				z-index: 1;
			}

			&:hover {
				.morelink__text {
					color: $blue;
				}

				.icon {
					color: $blue;
				}
			}
			&.less {
				// margin-top: 20px;

				.icon {
					transform: rotate(180deg);
				}

				&::before {
					opacity: 0;
				}
			}

			.icon {
				display: inline-block;
				margin-left: 5px;
				transition: all .3s;
				position: relative;
				z-index: 2;
			}

			&::before {
				content: '';
				position: absolute;
				left: 0;
				right: 0;
				top: -54px;
				height: 50px;
				// background: linear-gradient(to bottom, rgba(255,255,255,0) 0%,rgba(255,255,255,0.74) 74%,rgba(255,255,255,1) 100%);
				background: linear-gradient(to bottom, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 56%,rgba(255,255,255,1) 100%);
				transition: all .3s ease;
				z-index: 0;
			}
		}//morelink

		.morelink__text {
			line-height: 1;
			transition: all .3s;
		}
	}

}//feedback

.stars {

	&__item {
		width: 19px;
		height: 18px;
		display: inline-block;
		vertical-align: middle;
		background-repeat: no-repeat;
		background-position: 0 0;
		background-size: cover;

		&.check {
			background-image: url("../img/star-checked.svg");
		}

		&.unchecked {
			background-image: url("../img/star-unchecked.svg");
		}
	}
}

//========================================================
// %mobile
@media #{$mobileonly} {
	.feedbacks {
		&__col {
			margin-bottom: 20px;
			width: 100%;
		}
	}
}//@media (max-width: 767px) 

//========================================================

// %mobtab
@media #{$mobtabonly} {
	.feedbacks {

	}
}//@media (max-width: 1141px) 

//========================================================
// %tablet
@media #{$tabonly} {
	.feedbacks {
		&__inner {
			@include container-tab;
		}

		&__wrapper {
		}

		&__col {
			width: 50%;

			&:nth-child(odd) {
				padding-right: 15px;
			}

			&:nth-child(even) {
				padding-left: 15px;
			}
		}
	}
}//#{$tabonly}

//========================================================
// %tabdesk
@media #{$tabdeskonly} {
	.feedbacks {
		&__col {
			margin-bottom: 30px;
		}
	}
} //(min-width: 768px)

//========================================================
// %desktop
@media #{$deskonly} {
	.feedbacks {
		&__inner {
			@include container;
		}

		&__wrapper {
			margin-right: -30px;
		}

		&__col {
			width: 370px;
			margin-right: 30px;

		}


	}

	.feedback {

		&__content_text {
			// height: 172px;
			
		}
	}
} // (min-width: 1042px)

